import React, { useEffect, useState, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
    IconButton,
    Slider,
    Typography,
    Icon,
    Menu,
    MenuItem,
    CircularProgress,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import MicIcon from "@material-ui/icons/Mic";
import DescriptionIcon from "@material-ui/icons/Description";
import Carousel from "react-material-ui-carousel";

import AudioPlayer from "./AudioPlayer";
import { useAudioPlayer } from "../../hooks";
import { showNotification } from "../../redux/actions";
import {
    trackOpenArticleDescription,
    trackOpenSpeedsMenu,
    trackCloseSpeedsMenu,
    trackChangeSpeed,
    trackForward10s,
    trackRewind10s,
    trackPauseArticle,
    trackResumeArticle,
    trackShareArticle,
} from "../../util/analytics";

const useStyles = makeStyles(() => ({
    root: {
        display: "flex",
        flexDirection: "column",
        position: "fixed",
        top: 16,
        right: 16,
        borderRadius: 16,
    },
    controller: {
        height: 56,
        backgroundColor: "white",
        borderRadius: 32,
        marginBottom: 16,
        marginTop: 16,
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-evenly",
        alignItems: "center",
    },
    main: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        alignItems: "center",
        paddingTop: 48,
        paddingBottom: 48,
        paddingLeft: 24,
        paddingRight: 24,
        width: 400,
        height: "calc(100vh - 32px - 56px - 16px)",
        backgroundColor: "white",
        borderRadius: 16,
    },
    imageContainer: {
        display: "flex",
        width: "100%",
        justifyContent: "center",
        alignItems: "center",
        margin: 0,
        padding: 0,
    },
    primaryImage: {
        width: 200,
        height: 200,
        borderRadius: 8,
    },
    titleRow: {
        display: "flex",
        flexDirection: "row",
        width: "100%",
        alignItems: "center",
        marginTop: 32,
    },
    title: {
        fontSize: 16,
        textAlign: "left",
        paddingHorizontal: 16,
        width: "90%",
        maxWidth: "90%",
        fontWeight: 700,
    },
    namesRow: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        width: "100%",
        marginTop: 24,
    },
    more: {},
    author: {
        fontSize: 12,
        textAlign: "left",
        color: "#484848",
    },
    narrator: {
        fontSize: 12,
        textAlign: "left",
        color: "#484848",
    },
    progressBar: {
        width: "100%",
    },
    progressTime: {
        color: "#000000",
        fontSize: 12,
    },
    controlsContainer: {
        marginTop: 24,
        width: "100%",
    },
    actions: {
        marginTop: 32,
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-evenly",
        alignItems: "center",
    },
    timeContainer: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
    },
    playButton: {},
    buffering: {
        marginTop: 16,
        marginLeft: 8,
        marginRight: 8,
    },
    speed: {
        width: 48,
        height: 48,
        borderRadius: 24,
    },
    secondaryActions: {
        height: 56,
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        paddingHorizontal: 24,
        marginTop: 16,
    },
    action: {
        marginHorizontal: 16,
    },
    selectedSpeed: {
        fontWeight: 600,
        fontSize: 12,
    },
    unselectedSpeed: {
        fontSize: 14,
    },
    speedsMenu: {
        width: 120,
    },
}));

const toMMSS = (seconds) =>
    `${parseInt(Math.floor(seconds / 60), 10)
        .toString()
        .padStart(2, "0")}:${parseInt(seconds % 60, 10)
        .toString()
        .padStart(2, "0")}`;

const speeds = [
    {
        title: "0.25x",
        rate: 0.25,
    },
    {
        title: "0.5x",
        rate: 0.5,
    },
    {
        title: "0.75x",
        rate: 0.75,
    },
    {
        title: "Normal",
        rate: 1,
    },
    {
        title: "1.25x",
        rate: 1.25,
    },
    {
        title: "1.5x",
        rate: 1.5,
    },
    {
        title: "1.75x",
        rate: 1.75,
    },
    {
        title: "2x",
        rate: 2,
    },
];

const ArticlePlayer = () => {
    const classes = useStyles();
    const { id, title, author, narrator, imageURLs, audioURL } = useSelector(
        (state) => state.article.current,
    );
    const playerRef = useRef(null);
    const {
        currentTime,
        duration,
        playing,
        buffering,
        seek,
        play,
        pause,
        setPlaybackRate,
        playbackRate,
    } = useAudioPlayer(playerRef);
    const dispatch = useDispatch();
    const [speedsMenuAnchor, setSpeedsMenuAnchor] = useState(null);

    useEffect(() => {
        const { current } = playerRef;
        if (current) {
            current.load();
            play();
        }
    }, [id, play, playerRef]);

    const openTranscript = () => {
        trackOpenArticleDescription(id);
    };

    const openSpeedsMenu = (event) => {
        trackOpenSpeedsMenu(id);
        setSpeedsMenuAnchor(event.target);
    };

    const closeSpeedsMenu = () => {
        trackCloseSpeedsMenu(id);
        setSpeedsMenuAnchor(null);
    };

    const handlePlaybackSpeed = (rate) => () => {
        trackChangeSpeed(id, rate);
        setPlaybackRate(rate);
        setSpeedsMenuAnchor(null);
    };

    const handleRewind10 = () => {
        trackRewind10s(id);
        seek(Math.max(currentTime - 10, 0));
    };

    const handlePlayToggle = () => {
        if (playing) {
            pause();
            trackPauseArticle(id, "article-player");
        } else {
            play();
            trackResumeArticle(id, "article-player");
        }
    };

    const handleForward10 = () => {
        trackForward10s(id);
        seek(Math.min(currentTime + 10, duration));
    };

    const handleShare = () => {
        trackShareArticle(id);
        navigator.clipboard.writeText(`https://blogpod.io/articles/${id}`);
        dispatch(showNotification("Link copied to clipboard!", "success"));
    };

    const handleSliderChange = (_event, newValue) => {
        seek(newValue);
    };

    const renderControls = () => (
        <div className={classes.controller}>
            {!buffering && (
                <>
                    <IconButton onClick={handleRewind10}>
                        <Icon color="primary">replay_10</Icon>
                    </IconButton>
                    <IconButton onClick={handlePlayToggle}>
                        <Icon color="primary">
                            {playing ? "pause" : "play_circle_filled"}
                        </Icon>
                    </IconButton>
                    <IconButton onClick={handleForward10}>
                        <Icon color="primary">forward_10</Icon>
                    </IconButton>
                </>
            )}
            {buffering && (
                <CircularProgress
                    color="primary"
                    variant="indeterminate"
                    size={24}
                />
            )}
        </div>
    );

    return (
        <div className={classes.root}>
            <AudioPlayer
                playerRef={playerRef}
                audioURL={audioURL}
                type="audio/wav"
            />
            <div className={classes.main}>
                <div className={classes.imageContainer}>
                    {imageURLs.length === 1 && (
                        // eslint-disable-next-line jsx-a11y/alt-text
                        <img
                            className={classes.primaryImage}
                            src={imageURLs[0]}
                        />
                    )}
                    {imageURLs.length > 1 && (
                        <Carousel animation="slide" autoPlay={false}>
                            {imageURLs.map((imageURL) => (
                                // eslint-disable-next-line jsx-a11y/alt-text
                                <img
                                    key={imageURL}
                                    className={classes.primaryImage}
                                    src={imageURL}
                                />
                            ))}
                        </Carousel>
                    )}
                </div>

                <div className={classes.titleRow} onClick={openTranscript}>
                    <Typography className={classes.title}>{title}</Typography>
                    <IconButton>
                        <Icon
                            color="primary"
                            size={24}
                            className={classes.more}>
                            expand_more
                        </Icon>
                    </IconButton>
                </div>

                <div className={classes.namesRow}>
                    <DescriptionIcon
                        style={{ margin: 0, color: "#888888", fontSize: 16 }}
                    />
                    <Typography
                        variant="h6"
                        color="textSecondary"
                        style={{ fontSize: 12, marginLeft: 4 }}
                        component="h6">
                        {author.firstName} {author.lastName}
                    </Typography>
                    <MicIcon
                        style={{
                            margin: 0,
                            marginLeft: 16,
                            color: "#888888",
                            fontSize: 16,
                        }}
                    />
                    <Typography
                        variant="h6"
                        color="textSecondary"
                        style={{ fontSize: 12, marginLeft: 4 }}
                        component="h6">
                        {narrator.firstName} {narrator.lastName}
                    </Typography>
                </div>

                <div className={classes.controlsContainer}>
                    <Slider
                        className={classes.progressBar}
                        min={0}
                        max={duration}
                        value={currentTime}
                        onChange={handleSliderChange}
                    />

                    <div className={classes.timeContainer}>
                        <Typography className={classes.progressTime}>
                            {toMMSS(currentTime)}
                        </Typography>
                        <Typography className={classes.progressTime}>
                            {toMMSS(duration)}
                        </Typography>
                    </div>

                    <div className={classes.actions}>
                        <IconButton onClick={openSpeedsMenu}>
                            <Icon color="primary">slow_motion_video</Icon>
                        </IconButton>
                        {/* <IconButton>
              <Icon color="primary">favorite</Icon>
            </IconButton> */}
                        <IconButton onClick={handleShare}>
                            <Icon color="primary">share</Icon>
                        </IconButton>
                    </div>
                </div>
            </div>
            {renderControls()}
            <Menu
                anchorEl={speedsMenuAnchor}
                keepMounted={true}
                open={Boolean(speedsMenuAnchor)}
                onClose={closeSpeedsMenu}
                classes={{ paper: classes.speedsMenu }}>
                {speeds.map((speed) => (
                    <MenuItem
                        key={speed.title}
                        className={
                            playbackRate === speed.rate
                                ? classes.selectedSpeed
                                : classes.unselectedSpeed
                        }
                        onClick={handlePlaybackSpeed(speed.rate)}>
                        {speed.title}
                    </MenuItem>
                ))}
            </Menu>
        </div>
    );
};

export default ArticlePlayer;
