import {
  CREATE_ARTICLE_SUCCESS,
  CREATE_ARTICLE_FAIL,
  CREATE_ARTICLE,
  UPDATE_ARTICLE_SUCCESS,
  UPDATE_ARTICLE_FAIL,
  UPDATE_ARTICLE,
  FETCH_ARTICLE_SUCCESS,
  FETCH_ARTICLE_FAIL,
  FETCH_ARTICLE,
  PUBLISH_ARTICLE_SUCCESS,
  PUBLISH_ARTICLE_FAIL,
  PUBLISH_ARTICLE,
  UNPUBLISH_ARTICLE_SUCCESS,
  UNPUBLISH_ARTICLE_FAIL,
  UNPUBLISH_ARTICLE,
  UPLOAD_TO_SIGNED_URL_SUCCESS,
  UPLOAD_TO_SIGNED_URL_FAIL,
  UPLOAD_TO_SIGNED_URL,
  GET_UPLOAD_SIGNED_URL_SUCCESS,
  GET_UPLOAD_SIGNED_URL_FAIL,
  GET_UPLOAD_SIGNED_URL,
  SELECT_ARTICLE,
} from "../actionTypes";

const INITIAL_STATE = {
  current: null,
  error: null,
  uploadSignedURL: null,
  fetching: false,
  saving: false,
  publishing: false,
  uploading: false,
  generating: false,
};

const articleReducer = (state = INITIAL_STATE, action) => {
  const { type, payload } = action;
  switch (type) {
    case CREATE_ARTICLE_SUCCESS: {
      return {
        ...state,
        saving: false,
        current: payload,
      };
    }

    case CREATE_ARTICLE_FAIL: {
      return {
        ...state,
        saving: false,
        error: payload,
      };
    }

    case CREATE_ARTICLE: {
      return {
        ...state,
        saving: true,
        error: null,
      };
    }

    case UPDATE_ARTICLE_SUCCESS: {
      return {
        ...state,
        saving: false,
        current: payload,
      };
    }

    case UPDATE_ARTICLE_FAIL: {
      return {
        ...state,
        saving: false,
        error: payload,
      };
    }

    case UPDATE_ARTICLE: {
      return {
        ...state,
        saving: true,
        error: null,
      };
    }

    case FETCH_ARTICLE_SUCCESS: {
      return {
        ...state,
        current: payload,
        fetching: false,
      };
    }

    case FETCH_ARTICLE_FAIL: {
      return {
        ...state,
        error: payload,
        fetching: false,
      };
    }

    case FETCH_ARTICLE: {
      return {
        ...state,
        fetching: true,
        error: null,
      };
    }

    case PUBLISH_ARTICLE_SUCCESS: {
      return {
        ...state,
        current: payload,
        publishing: false,
      };
    }

    case PUBLISH_ARTICLE_FAIL: {
      return {
        ...state,
        error: payload,
        publishing: false,
      };
    }

    case PUBLISH_ARTICLE: {
      return {
        ...state,
        publishing: true,
        error: null,
      };
    }

    case UNPUBLISH_ARTICLE_SUCCESS: {
      return {
        ...state,
        current: payload,
        publishing: false,
      };
    }

    case UNPUBLISH_ARTICLE_FAIL: {
      return {
        ...state,
        error: payload,
        publishing: false,
      };
    }

    case UNPUBLISH_ARTICLE: {
      return {
        ...state,
        publishing: true,
        error: null,
      };
    }

    case UPLOAD_TO_SIGNED_URL_SUCCESS: {
      return {
        ...state,
        uploading: false,
      };
    }

    case UPLOAD_TO_SIGNED_URL_FAIL: {
      return {
        ...state,
        uploading: false,
        error: payload,
      };
    }

    case UPLOAD_TO_SIGNED_URL: {
      return {
        ...state,
        uploading: true,
        error: null,
      };
    }

    case GET_UPLOAD_SIGNED_URL_SUCCESS: {
      return {
        ...state,
        generating: false,
        uploadSignedURL: payload,
      };
    }

    case GET_UPLOAD_SIGNED_URL_FAIL: {
      return {
        ...state,
        generating: false,
        error: payload,
      };
    }

    case GET_UPLOAD_SIGNED_URL: {
      return {
        ...state,
        generating: true,
        uploadSignedURL: null,
        error: null,
      };
    }

    case SELECT_ARTICLE: {
      return {
        ...state,
        current: payload,
      };
    }

    default: {
      return state;
    }
  }
};

export default articleReducer;
