import {
    CREATE_ARTICLE_SUCCESS,
    CREATE_ARTICLE_FAIL,
    CREATE_ARTICLE,
    UPDATE_ARTICLE_SUCCESS,
    UPDATE_ARTICLE_FAIL,
    UPDATE_ARTICLE,
    FETCH_ARTICLE_SUCCESS,
    FETCH_ARTICLE_FAIL,
    FETCH_ARTICLE,
    FETCH_ARTICLES_SUCCESS,
    FETCH_ARTICLES_FAIL,
    FETCH_ARTICLES,
    UPLOAD_TO_SIGNED_URL_SUCCESS,
    UPLOAD_TO_SIGNED_URL_FAIL,
    UPLOAD_TO_SIGNED_URL,
    GET_UPLOAD_SIGNED_URL_SUCCESS,
    GET_UPLOAD_SIGNED_URL_FAIL,
    GET_UPLOAD_SIGNED_URL,
    PUBLISH_ARTICLE_SUCCESS,
    PUBLISH_ARTICLE_FAIL,
    PUBLISH_ARTICLE,
    UNPUBLISH_ARTICLE_SUCCESS,
    UNPUBLISH_ARTICLE_FAIL,
    UNPUBLISH_ARTICLE,
    FETCH_FEED_ARTICLES_SUCCESS,
    FETCH_FEED_ARTICLES_FAIL,
    FETCH_FEED_ARTICLES,
    FETCH_TRENDING_SUCCESS,
    FETCH_TRENDING_FAIL,
    FETCH_TRENDING,
    FETCH_PUBLIC_FEED_SUCCESS,
    FETCH_PUBLIC_FEED_FAIL,
    FETCH_PUBLIC_FEED,
    SELECT_ARTICLE,
} from "../actionTypes";
import * as client from "../../server/api";

// Create Article

const createArticleSuccess = (dispatch) => (article) => {
    dispatch({
        type: CREATE_ARTICLE_SUCCESS,
        payload: article,
    });
};

const createArticleFail = (dispatch) => (error) => {
    dispatch({
        type: CREATE_ARTICLE_FAIL,
        payload: error,
    });
};

export const createArticle = (data) => async (dispatch) => {
    try {
        dispatch({
            type: CREATE_ARTICLE,
            payload: null,
        });
        const article = await client.newArticle(data);
        createArticleSuccess(dispatch)(article);
    } catch (error) {
        createArticleFail(dispatch)("Failed to create article");
    }
};

