// Upload To Signed URL

const uploadToSignedURLSuccess = (dispatch) => () => {
    dispatch({
        type: UPLOAD_TO_SIGNED_URL_SUCCESS,
        payload: null,
    });
};

const uploadToSignedURLFail = (dispatch) => (error) => {
    dispatch({
        type: UPLOAD_TO_SIGNED_URL_FAIL,
        payload: error,
    });
};

export const uploadToSignedURL = (signedURL, data) => async (dispatch) => {
    try {
        dispatch({
            type: UPLOAD_TO_SIGNED_URL,
            payload: null,
        });
        await client.uploadToSignedURL(signedURL, data);
        uploadToSignedURLSuccess(dispatch)();
    } catch (error) {
        uploadToSignedURLFail(dispatch)("Failed to upload to signed URL");
    }
};

