import {
    FETCH_ARTICLE_TAGS_SUCCESS,
    FETCH_ARTICLE_TAGS_FAIL,
    FETCH_ARTICLE_TAGS,
} from "../actionTypes";
import * as client from "../../server/api";

const fetchArticleTagsSuccess = (dispatch) => (article) => {
    dispatch({
        type: FETCH_ARTICLE_TAGS_SUCCESS,
        payload: article,
    });
};

const fetchArticleTagsFail = (dispatch) => (error) => {
    dispatch({
        type: FETCH_ARTICLE_TAGS_FAIL,
        payload: error,
    });
};

/* eslint-disable-next-line import/prefer-default-export */
export const fetchArticleTags = (params) => async (dispatch) => {
    try {
        dispatch({
            type: FETCH_ARTICLE_TAGS,
            payload: params,
        });
        const article = await client.getArticleTags(params);
        fetchArticleTagsSuccess(dispatch)(article);
    } catch (error) {
        fetchArticleTagsFail(dispatch)("Failed to fetch article");
    }
};
