export const uploadToSignedURL = async (signedURL, data) => {
    await axios.put(signedURL, data, {
        headers: {
            "Content-Type": "application/octet-stream",
        },
    });
};

export const getUploadSignedURL = async (slug) => {
    const response = await axios.get(`/articles/${slug}/upload-signed-url`);
    return response.data.uploadSignedURL;
};

export const publishArticle = async (articleId) => {
    const response = await axios.patch(`/articles/${articleId}/publish`);
    return initializeArticle(response.data);
};

export const unpublishArticle = async (articleId) => {
    const response = await axios.patch(`/articles/${articleId}/unpublish`);
    return initializeArticle(response.data);
};

export const getArticleTags = async () => {
    const response = await axios.get("/articles/tags");
    return response.data;
};
