import {
  FETCH_PUBLIC_FEED_SUCCESS,
  FETCH_PUBLIC_FEED_FAIL,
  FETCH_PUBLIC_FEED,
} from "../actionTypes";

const INITIAL_STATE = {
  list: [],
  fetching: false,
  error: null,
};

const publicFeedReducer = (state = INITIAL_STATE, action) => {
  const { type, payload } = action;
  switch (type) {
    case FETCH_PUBLIC_FEED_SUCCESS: {
      return {
        ...state,
        list: payload,
        fetching: false,
      };
    }

    case FETCH_PUBLIC_FEED_FAIL: {
      return {
        ...state,
        fetching: false,
        error: payload,
      };
    }

    case FETCH_PUBLIC_FEED: {
      return {
        ...state,
        fetching: true,
        error: null,
      };
    }

    default: {
      return state;
    }
  }
};

export default publicFeedReducer;
