import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { matchRoutes } from "react-router-config";
import isEqual from "react-fast-compare";

import { FETCH_SESSION_FAIL } from "../redux/actionTypes";
import { fetchSession, setSettings } from "../redux/actions";

import layouts from "../layouts";
import routes from "../routes";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.default,
    color: theme.palette.text.primary,
  },
}));

const defaultSettings = {
  layout: {
    name: "visitor",
    protected: false,
  },
};

const PrimaryLayout = (props) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const location = useLocation();
  const { payload, status } = useSelector((state) => state.session);
  const settings = useSelector((state) => state.settings) || defaultSettings;

  useEffect(() => {
    const matched = matchRoutes(routes, location.pathname)[0];

    if (matched?.route.settings) {
      const routeSettings = { ...defaultSettings, ...matched.route.settings };
      if (!isEqual(settings, routeSettings)) {
        dispatch(setSettings(routeSettings));
      }
    }
  }, [dispatch, location.pathname, settings]);

  useEffect(() => {
    if (!payload && !status) {
      dispatch(fetchSession());
    }

    if (settings.protected && !payload && status === FETCH_SESSION_FAIL) {
      history.push("/login");
    }
  }, [payload, status, dispatch, history, settings.protected]);

  const Layout = layouts[settings.layout.name];
  return <Layout className={classes.root} settings={settings} {...props} />;
};

export default PrimaryLayout;
