import { combineReducers } from "redux";

import notificationReducer from "./notification";
import articlesReducer from "./articles";
import articleReducer from "./article";
import sessionReducer from "./session";
import feedArticlesReducer from "./feedArticles";
import settingsReducer from "./settings";
import trendingReducer from "./trending";
import publicFeedReducer from "./publicFeed";
import usersReducer from "./users";
import articleTagsReducer from "./articleTags";

const rootReducer = combineReducers({
    notification: notificationReducer,
    articles: articlesReducer,
    article: articleReducer,
    session: sessionReducer,
    feedArticles: feedArticlesReducer,
    settings: settingsReducer,
    trending: trendingReducer,
    publicFeed: publicFeedReducer,
    users: usersReducer,
    articleTags: articleTagsReducer,
});

export default rootReducer;
