import {
  FETCH_TRENDING_SUCCESS,
  FETCH_TRENDING_FAIL,
  FETCH_TRENDING,
} from "../actionTypes";

const INITIAL_STATE = {
  list: [],
  fetching: false,
  error: null,
};

const trendingReducer = (state = INITIAL_STATE, action) => {
  const { type, payload } = action;
  switch (type) {
    case FETCH_TRENDING_SUCCESS: {
      return {
        ...state,
        list: payload,
        fetching: false,
      };
    }

    case FETCH_TRENDING_FAIL: {
      return {
        ...state,
        fetching: false,
        error: payload,
      };
    }

    case FETCH_TRENDING: {
      return {
        ...state,
        fetching: true,
        error: null,
      };
    }

    default: {
      return state;
    }
  }
};

export default trendingReducer;
