import {
  FETCH_FEED_ARTICLES_SUCCESS,
  FETCH_FEED_ARTICLES_FAIL,
  FETCH_FEED_ARTICLES,
} from "../actionTypes";

const INITIAL_STATE = {
  list: [],
  fetching: false,
  error: null,
};

const feedArticlesReducer = (state = INITIAL_STATE, action) => {
  const { type, payload } = action;
  switch (type) {
    case FETCH_FEED_ARTICLES_SUCCESS: {
      return {
        ...state,
        list: payload,
        fetching: false,
      };
    }

    case FETCH_FEED_ARTICLES_FAIL: {
      return {
        ...state,
        fetching: false,
        error: payload,
      };
    }

    case FETCH_FEED_ARTICLES: {
      return {
        ...state,
        fetching: true,
        error: null,
      };
    }

    default: {
      return state;
    }
  }
};

export default feedArticlesReducer;
