import {
    FETCH_ARTICLE_TAGS_SUCCESS,
    FETCH_ARTICLE_TAGS_FAIL,
    FETCH_ARTICLE_TAGS,
} from "../actionTypes";

const initialState = {
    list: [],
    map: {},
    error: null,
    fetching: false,
};

const articleReducer = (state = initialState, action) => {
    const { type, payload } = action;
    switch (type) {
        case FETCH_ARTICLE_TAGS_SUCCESS: {
            const map = {};
            /* eslint-disable-next-line no-restricted-syntax */
            for (const tag of payload) {
                map[tag.id] = tag;
            }
            return {
                ...state,
                fetching: false,
                map,
                list: payload,
            };
        }

        case FETCH_ARTICLE_TAGS_FAIL: {
            return { ...state, fetching: false, error: payload };
        }

        case FETCH_ARTICLE_TAGS: {
            return { ...state, fetching: true };
        }

        default: {
            return state;
        }
    }
};

export default articleReducer;
