import { useState, useEffect, useCallback } from "react";

const useAudioPlayer = (playerRef) => {
    const [duration, setDuration] = useState(0);
    const [currentTime, setCurrentTime] = useState(0);
    const [playing, setPlaying] = useState(false);
    const [buffering, setBuffering] = useState(false);

    const play = useCallback(() => {
        playerRef.current?.play();
    }, [playerRef]);

    const pause = useCallback(() => {
        playerRef.current?.pause();
    }, [playerRef]);

    const setPlaybackRate = useCallback(
        (rate) => {
            if (playerRef.current) {
                /* eslint-disable-next-line no-param-reassign */
                playerRef.current.playbackRate = rate;
            }
        },
        [playerRef],
    );

    const seek = useCallback(
        (time) => {
            if (playerRef.current) {
                /* eslint-disable-next-line no-param-reassign */
                playerRef.current.currentTime = time;
            }
        },
        [playerRef],
    );

    useEffect(() => {
        if (!playerRef.current) {
            return undefined;
        }

        const audio = playerRef.current;

        const onDataLoaded = () => {
            setDuration(audio.duration);
            setCurrentTime(audio.currentTime);
        };

        const onTimeUpdate = () => {
            setCurrentTime(audio.currentTime);
        };

        const onBuffering = () => {
            setBuffering(true);
            setPlaying(false);
        };

        const onPlay = () => {
            setBuffering(false);
            setPlaying(true);
        };

        const onPause = () => {
            setBuffering(false);
            setPlaying(false);
        };

        audio.addEventListener("loadeddata", onDataLoaded);
        audio.addEventListener("timeupdate", onTimeUpdate);
        audio.addEventListener("waiting", onBuffering);
        audio.addEventListener("play", onPlay);
        audio.addEventListener("playing", onPlay);
        audio.addEventListener("pause", onPause);

        // effect cleanup
        return () => {
            audio.removeEventListener("loadeddata", onDataLoaded);
            audio.removeEventListener("timeupdate", onTimeUpdate);
            audio.removeEventListener("waiting", onBuffering);
            audio.removeEventListener("play", onPlay);
            audio.removeEventListener("playing", onPlay);
            audio.removeEventListener("pause", onPause);
        };
    }, [playing, currentTime, playerRef]);

    return {
        currentTime,
        duration,
        playing,
        buffering,
        seek,
        play,
        pause,
        setPlaybackRate,
        playbackRate: playerRef.current?.playbackRate || 1,
    };
};

export default useAudioPlayer;
